import { LocationSaveService } from './../Services/locationSave/location-save.service';
import { AndroidPermissions } from '@ionic-native/android-permissions/ngx';
import { LocationAccuracy } from '@ionic-native/location-accuracy/ngx';
import { CateogoryService } from './../Services/Category/cateogory.service';
import { SubCategoryService } from './../Services/Subcategory/sub-category.service';
import { appSetting } from './../app-setting';
import { Component } from '@angular/core';

@Component({
  selector: 'app-tabs',
  templateUrl: 'tabs.page.html',
  styleUrls: ['tabs.page.scss']
})
export class TabsPage {

  constructor(public appSetting:appSetting,
    private categoryService:CateogoryService,
    private SubCategoryService:SubCategoryService,private locationService:LocationSaveService) {
    this.categoryService.get().subscribe(
      x => (this.appSetting.categoryMenu = x),
      err => console.log(err),
      () => {
        this.SubCategoryService.get().subscribe(
          y=>(this.appSetting.subCategoryMenu=y)
        )
      }
    );
    this.locationService.checkGPSPermission();
  }
 
    
}
