import { appSetting } from 'src/app/app-setting';
import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs";
import { subCategoryModel } from "./../../model/subCategoryModel";

@Injectable({
  providedIn: "root"
})
export class SubCategoryService {
  private url = `${this.appSetting.http}://${this.appSetting.apiPort}/api/subcategoryModels`;
  private httpOptions = {
    headers: new HttpHeaders({
      "Content-Type": "application/json"
    })
  };
  constructor(private http: HttpClient,private appSetting:appSetting) {}
  get(): Observable<subCategoryModel[]> {
    return this.http.get<subCategoryModel[]>(this.url);
  }
  getSingle(id: number): Observable<subCategoryModel> {
    const searchUrl = `${this.url}/${id}`;
    return this.http.get<subCategoryModel>(searchUrl);
  }
  post(data: subCategoryModel): void {
    this.http.post(this.url, data, this.httpOptions).subscribe(
      res => {
        console.log(res);
      },
      err => {
        console.log(err);
      }
    );
  }

  put(data: subCategoryModel): void {
    const searchUrl = `${this.url}/${data.cat_id}`;
    this.http.put(searchUrl, data, this.httpOptions).subscribe(
      res => {
        console.log(res);
      },
      err => {
        console.log(err);
      }
    );
  }

  delete(id: number): void {
    var data = null;
    var xhr = new XMLHttpRequest();
    xhr.withCredentials = false;

    xhr.addEventListener("readystatechange", function() {
      if (this.readyState === this.DONE) {
        console.log(this.responseText);
      }
    });

    xhr.open("DELETE", this.url + "/" + id);

    xhr.send(data);
  }
}
