import { LocationAccuracy } from '@ionic-native/location-accuracy/ngx';
import { AndroidPermissions } from '@ionic-native/android-permissions/ngx';
import { locationModel } from './../../model/locationModel';
import { Injectable } from '@angular/core';
import { appSetting } from 'src/app/app-setting';
import { Observable } from "rxjs";
import { HttpHeaders } from "@angular/common/http";
import { HttpClient } from "@angular/common/http";
@Injectable({
  providedIn: 'root'
})
export class LocationSaveService {
  private url = `${this.appSetting.http}://${this.appSetting.apiPort}/api/locationModels`;
  private httpOptions = {
    headers: new HttpHeaders({
      "Content-Type": "application/json"
    })
  };
  constructor(private androidPermissions: AndroidPermissions,
    private locationAccuracy: LocationAccuracy,
    private http: HttpClient,private appSetting:appSetting) {}


  get(id:number): Observable<locationModel> {
    const searchUrl = `${this.url}/${id}`;
    return this.http.get<locationModel>(searchUrl);
  }

  post(data: locationModel){
  
    let x:locationModel;
    this.http.post(this.url, data, this.httpOptions).subscribe(
      res => {
   
        console.log(res);
      },
      err => {
        console.log(err);
      },
      () => {
       }
    );
  }

  put(data: locationModel): void {
    const searchUrl = `${this.url}/${data.userID}`;
    this.http.put(searchUrl, data, this.httpOptions).subscribe(
      res => {
        console.log(res);
      },
      err => {
        console.log(err);
      }
    );
  }

    //Check if application having GPS access permission  
    checkGPSPermission() {
      this.androidPermissions.checkPermission(this.androidPermissions.PERMISSION.ACCESS_COARSE_LOCATION).then(
        result => {
          if (result.hasPermission) {
   
            //If having permission show 'Turn On GPS' dialogue
            this.askToTurnOnGPS();
          } else {
   
            //If not having permission ask for permission
            this.requestGPSPermission();
          }
        },
        err => {
          console.log(err);
        }
      );
    }
    requestGPSPermission() {
      this.locationAccuracy.canRequest().then((canRequest: boolean) => {
        if (canRequest) {
          console.log("4");
        } else {
          //Show 'GPS Permission Request' dialogue
          this.androidPermissions.requestPermission(this.androidPermissions.PERMISSION.ACCESS_COARSE_LOCATION)
            .then(
              () => {
                // call method to turn on GPS
                this.askToTurnOnGPS();
              },
              error => {
                //Show console.log if user click on 'No Thanks'
                console.log('requestPermission Error requesting location permissions ' + error)
              }
            );
        }
      });
    }
   
    askToTurnOnGPS() {
      this.locationAccuracy.request(this.locationAccuracy.REQUEST_PRIORITY_HIGH_ACCURACY).then(
        () => {
          // When GPS Turned ON call method to get Accurate location coordinates
          
        },
        error => console.log('Error requesting location permissions ' + JSON.stringify(error))
      );
    }
}
